<template>
    <div class="blogger-articles-container">
      <v-row dense class="blogger-articles-row">
        <v-col
          v-for="(item, index) in articlesData"
          :key="item.title"
          cols="12" md="3"
          class="project-col">
            <v-card width="200" height="270" class="mx-auto blogger-card" @click="toBlogContent(item)">
                <v-list-item>
                    <v-list-item-avatar color="#C5E1A5" size="24"><b class="avatar-text">{{ index | cookAvatar }}</b></v-list-item-avatar>
                    <v-list-item-content>
                        <v-list-item-title><span class="blog-title">{{ item.title | cookTitle }}</span></v-list-item-title>
                        <v-list-item-subtitle><span class="blog-subtitle">{{ item.date }}</span></v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
                <v-img :src="index | cookImg" height="160" contain></v-img>
                <v-list-item-action  class="blogger-card-action">
                    <v-row style="margin-left:10px">
                        <v-btn icon shaped small tile class="blogger-card-btn" v-for="tag in item.tags" :key="tag.name">
                            <v-tooltip bottom>
                                <template v-slot:activator="{ on, attrs }">
                                    <v-avatar size="12" dark><v-img sizes="12" contain :src="tag.icon | cookTag" v-bind="attrs" v-on="on"></v-img></v-avatar>
                                </template>
                                <span>{{ tag.name }}</span>
                            </v-tooltip>
                        </v-btn>
                    </v-row>
                </v-list-item-action>
            </v-card>
        </v-col>
      </v-row>
    </div>
</template>

<script>

export default {
    components: { 
    },
    filters: {
        cookAvatar(value){
            let avatar = ["A", "B", "C", "D", "E", "F", "G", "H", "I", "J", "K", "L", "M", "N", "O", "P", "Q"]
            try {
                if(0 <= value <= 10){
                    return " " + avatar[value]; 
                } 
            } catch (error) {
                return " W"
            }
        },
        cookTitle(value){
            if(value && value.length > 16){
                return value.substring(0, 17) + "...";
            }
            return value;
        },
        cookImg(value){
            try {
               let index =  Math.ceil(Math.random()*210);
               let jpg = require("../assets/imgs/ming/clothing"+index+".jpg");
               return jpg;
            } catch (error) {
                let jpg = require("../assets/imgs/ming/clothing212.jpg")
                return jpg;
            }
            
        },
        cookTag(value) {
            try {
               let jpg = require("../assets/imgs/language/"+value);
               return jpg;
            } catch (error) {
                let jpg = require("../assets/imgs/language/leaf.png")
                return jpg;
            }
        }
    },
    data() {
        return {
            sourceTag:"notes",
            sourceType:"学习笔记",
            articlesData:[],
            blogList:[],
            relation:{
              'notes':"学习笔记",
              'solution':"解决方案",
              'collection':"转载",
              'secret':"个人随笔",
              'python':"python",
              'java':"java",
              'db':"数据库",
              'js':"js",
              'vue':"vue",
              'bigdata':"大数据",
              'deeplearn':"深度学习",
            }
        }
    },
    methods: {
        toBlogContent(item){
            let path = item.path;
            this.$router.push({path:"/articles"+path});
        },
        async getArticlesData(){
            let _this = this;
            let query = _this.$router.currentRoute.query;
            if(query){
                let type = query.type;
                _this.sourceTag = type;
                _this.articlesData = [];
                let value = _this.relation[type];
                if(value){
                    _this.sourceType = value;
                    let data = await import("@/config/articles/"+ type +".js");
                    _this.articlesData = data.NOTES;
                    return
                }
            }
            
        }
    },
    mounted() {
        let _this = this;
        _this.getArticlesData();
    },
}
</script>

<style lang="stylus" scoped>
.blogger-articles-container
  position absolute
  top  0
  bottom 0
  border 0
  left 0
  width  100%
  height  100%
  overflow  auto
  background-size  cover
  background-image url("../assets/imgs/bg/4.jpg")

.blogger-card
  background-color #E0F7FA

.blog-title
  font-size 14px

.blog-subtitle
  font-size 10px

.avatar-text
  color  #E65100
  margin-left 32%

.blogger-card-action
    height 36px
    overflow  auto
    background-color inherit

.blogger-card-btn
    background-color inherit
    margin-right  5px
</style>